export default {
	props: {
		weeks: {
			type: Object,
			default () {
				return {}
			}
		},
		calendar: {
			type: Object,
			default: () => {
				return {}
			}
		},
		selected: {
			type: Array,
			default: () => {
				return []
			}
		},
		lunar: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isChecked: false
		}
	},
	watch: {
		calendar() {
			console.log('calendar')
			this.isChecked = false
			if (this.calendar.fullDate === this.weeks.fullDate && !this.weeks.isDay)
				this.isChecked = true
		}
		
	},
	methods: {
		choiceDate(weeks) {
			// console.log(weeks)
			
			this.$emit('change', weeks)
		}
	}
}
